<template>
  <div class="row">
    <div class="col-sm-12">
      <div class="progress">
        <div
          class="progress-bar"
          role="progressbar"
          aria-valuenow="60"
          aria-valuemin="0"
          aria-valuemax="100"
          :style="{width: (quoteCount / maxQuotes) * 100 + '%'}">
        {{ quoteCount }} / {{ maxQuotes }}
        </div>
      </div>
    </div>
    <h3>Quotes Added</h3>
  </div>
</template>

<script>
export default {
  props: ['quoteCount', 'maxQuotes']
}
</script>
